import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { ObjectHelper } from './../../helpers/ObjectHelper';
import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

export type PlanningConstraintsState = EntityState<PlanningConstraint>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'planning-constraint' })
export class PlanningConstraintsStore extends EntityStore<PlanningConstraintsState>{
  constructor() {
    super();
  }

  public setConstraintParamValue(constraintId: number, updatedParams: PlanningConstraint['params']): void {
    this.update(constraintId, constraint => {
      const newConstraint = ObjectHelper.copyObjectAttributes(undefined, constraint, true);
      Object.keys(updatedParams).forEach(key => {
        newConstraint.setParams(key, updatedParams[key]);
      });
      return newConstraint;
    });
  }

  public setConstraintStateValue(constraintId: number, isEnable: boolean): void {
    this.update(constraintId, constraint => {
      const newConstraint = ObjectHelper.copyObjectAttributes(undefined, constraint, true);
      newConstraint.setIsEnable(isEnable);
      return newConstraint;
    });
  }
}
