import { JobTitle } from './planning/job-title.model';
import { identifier, serializable } from 'serializr';
import { ObjectHelper } from '../helpers/ObjectHelper';
export class Person {

    @serializable(identifier())
    public id: number;

    @serializable
    public firstName: string;

    @serializable
    public lastName: string;

    @serializable
    public avatarUrl: string;

    @serializable
    public status: string;

    @serializable
    public jobTitle: JobTitle['id'] = 0;

    @serializable
    public workingPercentage: number;

    @serializable
    public service: string;

    @serializable
    public gender: string;

    @serializable
    public skill: string;

    constructor(init: Partial<Person>) {
        if(init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId() { return this.id;}

    public getFirstname() { return this.firstName;}

    public getLastname() { return this.lastName;}

    public getAvatarUrl() { return this.avatarUrl;}

    public getJobTitleId() { return this.jobTitle;}

    public getWorkingPercentage() { return this.workingPercentage;}

    public getService() { return this.service;}

    public getPersonStatus(): string { return this.status; }

    public getGender(): string { return this.gender; }

    public getSkill(): string { return this.skill; }
}
