import { getDefaultModelSchema } from 'serializr';

export class ObjectHelper {

  /**
   * Use negative incremental index for generated ID to prevent conflict with APIs.
   */
  private static identifierIndex = -1;

  /**
   * @function
   * @description Copy object Attributes of 'objectToCopy' to 'objectDestination'
   */
  public static copyObjectAttributes<T>(objectDestination: T, objectSource: Record<string, unknown> | T, deepCopy = false): T {
    // Get the scheme define with serializr decorators
    const modelScheme = getDefaultModelSchema(objectDestination ?? objectSource);
    let result = objectDestination;
    // if objectDestination is null, create a new one by calling its constructor :
    if(objectDestination === undefined && objectSource.constructor) {
      result = new (objectSource.constructor as new() => T)();
    }
    // Then, for each attribute, try to copy the value :
    Object.keys(objectSource)
      // Filter elements not decorated with serializr  (if a serialzr model exist)
      .filter(key => modelScheme === undefined
                      || (key in modelScheme.props)
                      || (modelScheme.extends?.props && key in modelScheme.extends?.props))
      // filter functions:
      .filter(key => typeof result[key] !== 'function')
      // finally copy the value :
      .forEach(key => {
        if(deepCopy) {
          // Deep copying depending of type of the object :
          if(objectSource[key] instanceof Date) {
            result[key] = new Date(objectSource[key].getTime());
          }
          else if(typeof objectSource[key] === 'object') {
            result[key] = this.copyObjectAttributes(undefined, objectSource[key], true);
          } else {
            result[key] = objectSource[key];
          }
        }
        else {
          // shallow copy :
          result[key] = objectSource[key];
        }
      });
      return result;
  }

  static generateUniqueId(): number {
    // Use negative incremental index for generated ID to prevent conflict with APIs :
      return ObjectHelper.identifierIndex--;
  }
}
