import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { identifier, list, object, primitive, serializable } from 'serializr';
import { AbstractShift } from './planning/abstract-shift.model';
export class Unit {

    @serializable(identifier())
    public id: number;

    @serializable
    public name: string;

    @serializable
    public eso: string;

    @serializable(list(primitive()))
    public planners?: Array<string>;

    constructor(init: Partial<Unit>) {
        Object.assign(this, init);
    }

    public getId() { return this.id;}

    public getName() { return this.name;}

    public getEso() { return this.eso;}

    public getPlanners() { return this.planners;}
}
